html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
}

.footer {
  background-color: #2e0b7c;
  color: #fff;
  padding: 1rem;
  margin-top: auto; /* Move the footer to the bottom */
}

.homepage {
  background-image: url('../img/Background_img.jpg');
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
    color: white;
  font-family: Arial, sans-serif;
  img{
    opacity: .5;
  }
}
.homepage::before {
  content: "";
  background-image: url('../img/Background_img.jpg');
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5; /* Adjust the opacity as needed */
  z-index: -1;
}

